import request from "superagent/superagent";
import * as actionTypes from './actionTypes';
import * as constant from './../../shared/constant';
import * as utility from './../../shared/utility';
import * as action from './index';


export const getNotificationList = (payload) => {
    return {
        type: actionTypes.GET_NOTIFICATION_HISTORY,
        payload: payload
    };
}
export const getTransactionHistory = (payload) => {
    return {
        type: actionTypes.GET_TRANSACTION_HISTORY,
        payload: payload
    };
}
export const addDispute = (transactionList) => {
    return {
        type: actionTypes.ADD_DISPUTE,
        payload: transactionList
    };
}
export const getDisputeReasonQuestions = () => {
    return {
        type: actionTypes.GET_DISPUTE_REASON_QUESTIONS,
        payload: constant.GET_DISPUTE_REASON_QUESTIONS
    };
}
export const setDisputeReasonAnswer = (answer) => {
    return {
        type: actionTypes.SET_DISPUTE_REASON_ANSWER,
        payload: answer
    };
}
export const addCreditCardQuestionAnswer = (answers) => {
    return {
        type: actionTypes.ADD_CREDIT_CARD_QUESTION_ANSWER,
        payload: answers
    };
}
export const setAdditionalQuestionsAnswer = (payload) => {
    return {
        type: actionTypes.SET_ADDITIONAL_QUESTIONS_ANSWER,
        payload: payload
    };
}
export const setExtraInfo = (payload) => {
    return {
        type: actionTypes.SET_EXTRA_INFO,
        payload: payload
    };
}
export const submitDisputeResponse = (payload) => {
    return {
        type: actionTypes.SUBMIT_DISPUTE,
        payload: payload
    };

}
export const restCallSuccess = () => {
    return {
        type: actionTypes.REST_CALL_SUCCESS
    };
};
export const restCallStart = () => {
    return {
        type: actionTypes.REST_CALL_START
    };
};
export const restCallFail = (error) => {
    return {
        type: actionTypes.REST_CALL_FAIL,
        error: error
    };
};
export const userProfile = (payload) => {
    return {
        type: actionTypes.GET_USER_PROFILE,
        payload: payload
    };
};

export const setExpanded = (payload) => {
    return {
        type: actionTypes.SET_EXPANDED,
        payload: payload
    };
};

export const getComments = (payload) => {
    return {
        type: actionTypes.GET_COMMENTS,
        payload: payload
    };
};

export const setInputComment = (payload) => {
    return {
        type: actionTypes.SET_INPUT_COMMENTS,
        payload: payload
    };
};

export const setCaseInfo = (payload) => {
    return {
        type: actionTypes.SET_CASE_INFO,
        payload: payload
    };
};

export const setCaseId = (payload) => {
    return {
        type: actionTypes.CASE_ID,
        payload: payload
    };
};

export const setAccessToken = (payload) => {
    return {
        type: actionTypes.ACCESS_TOKEN,
        payload: payload
    };
};

export const setCaseFile= (payload) => {
    return {
        type: actionTypes.SET_CASE_FILE,
        payload: payload
    };
};




