import React, { Component } from 'react';
import { connect } from 'react-redux';
import * as actions from '../../state/actions';
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import Typography from '@material-ui/core/Typography';



class Comments extends Component {

    state = {
        expanded: null,
        inputComment: "",
        file: '',
        binaryCode: ''
    };

    componentDidMount = () => {

        const caseId = this.props.caseId;
        const accessToken = this.props.accessToken;
        // console.log(accessToken);
        this.props.remoteGetComments(accessToken, caseId);
    }

    handleChange = panel => (event, expanded) => {
        this.setState({
            expanded: expanded ? panel : false,
        });
    };

    handleInputComment = (e) => {
        var myValue = e.target.value;
        this.setState({ inputComment: myValue });
    }

    submitComment = () => {
        const caseId = this.props.caseId;
        const accessToken = this.props.accessToken;
        var input = {};
        // console.log(this.props.userProfile);
        if (this.props.userProfile.name != undefined) {
            input.author = this.props.userProfile.name + " " + this.props.userProfile.lname;
        } else {
            input.author = "Anonymous";
        }
        input.text = this.state.inputComment;

        this.props.remotePostComments(accessToken, caseId, JSON.stringify(input));
    }


    handleFile = (e) => {
        var that = this;
        e.preventDefault();
        let file = e.target.files[0];
        // var encoded = window.btoa(file);
        var reader = new FileReader();
        reader.readAsBinaryString(file);

        reader.onload = function () {
            //   console.log(btoa(reader.result));
            var fileBinary = btoa(reader.result);

            that.setState({
                binaryCode: fileBinary
            });
        };
        reader.onerror = function () {
            console.log('there are some problems');
        };
        that.setState({
            file: file
        });
    }

    handleFileSubmit = (e) => {
        const caseId = this.props.caseId;
        const accessToken = this.props.accessToken;
        e.preventDefault();
        //console.log('handle uploading-', this.state.binaryCode);
        this.props.remotePostAttachment(accessToken, caseId, 'Posted from React', this.state.file.name, this.state.file.size, this.state.binaryCode);
    }
    render() {
        const { expanded } = this.state;
        return (
            <div>
                <ExpansionPanel className=""
                    expanded={expanded === "commentPanel"}
                    onChange={this.handleChange("commentPanel")}
                    style={{ paddingTop: '2%', paddingBottom: '2%', color: '#ffffff', backgroundColor: '#63BE59', width: '100%' }
                    }>
                    <ExpansionPanelSummary expandIcon="">
                        <Typography className="Typography" style={{ color: "white", fontSize: "16px" }}> Add a note or comment </Typography>
                    </ExpansionPanelSummary>
                    <ExpansionPanelDetails>
                        <div style={{ backgroundColor: '#babdc1', overflowY: 'scroll', height: '90px', width: "100%" }}>
                            <Typography className="TypographyText" style={{ color: "black", fontSize: "12px" }}>
                                Comments: <br />
                                <span>
                                    {
                                        this.props.comments.length ? this.props.comments.map((itemTestArray) =>
                                            (<span key={itemTestArray}> {itemTestArray} <br /></span>)) : '-'
                                    }
                                </span>
                            </Typography>
                        </div>
                    </ExpansionPanelDetails>
                    <form>
                        &nbsp; &nbsp;
                        <input type="text" style={{ color: "black" }} name="inputComment" size="38" value={this.state.inputComment} onChange={e => this.handleInputComment(e)} />&nbsp; &nbsp;
                        {
                            this.state.inputComment.length > 0 ?
                                (<button type="button" className="btn btn-primary" style={{ backgroundColor: "#19AA89", color: "#fff" }} onClick={(e) => this.submitComment(e)}>Make a comment</button>)
                                :
                                <button type="button" disabled className="btn btn-primary" style={{ backgroundColor: "#19AA89", color: "#fff" }} onClick={(e) => this.submitComment(e)}>Make a comment</button>
                        }
                    </form>
                </ExpansionPanel>

                <div style={{ minHeight: '10px' }}></div>
                <ExpansionPanel className=""
                    expanded={expanded === "attachmentPanel"}
                    onChange={this.handleChange("attachmentPanel")}
                    style={{ paddingTop: '2%', paddingBottom: '2%', color: '#ffffff', backgroundColor: '#63BE59', width: '100%' }
                    }>
                    <ExpansionPanelSummary expandIcon="">
                        <Typography className="Typography" style={{ color: "white", fontSize: "16px" }}>Attach Document(s)</Typography>
                    </ExpansionPanelSummary>
                    <ExpansionPanelDetails>
                        <form>
                            <div style={{
                                width: "100%",
                                backgroundColor: "white",
                                padding: '15px',
                                color: 'black'
                            }}>
                                <input className="fileInput" type="file" onChange={(e) => this.handleFile(e)}></input>
                                {
                                    this.state.file != null > 0 ?
                                        <p> Size: {this.state.file.size} Bytes </p>
                                        :
                                        null
                                }
                                <p>------------<br />
                                    Name: {this.state.file.name} <br /></p>
                            </div>
                            <br />
                            <div style={{ paddingLeft: '73%' }}>
                                <button type="button" className="btn btn-primary"
                                    onClick={(e) => this.handleFileSubmit(e)}>Submit</button>
                            </div>
                        </form>
                    </ExpansionPanelDetails>
                </ExpansionPanel>
                <div style={{ minHeight: '95px' }}></div>
                <button className="btn btn-success" type="button" value="Input" style={{ paddingTop: '2%', paddingBottom: '2%', color: '#ffffff', backgroundColor: '#189697', width: '100%' }}>
                    <p style={{ fontSize: "18px", textAlign: "left", paddingLeft: "10px", fontWeight: "400" }} >Cancel Dispute</p>
                </button>
            </div>
        );
    }
}
const mapStateToProps = state => {
    return {
        userProfile: state.transactionDisputeReducer.userProfile,
        expanded: state.transactionDisputeReducer.expanded,
        comments: state.transactionDisputeReducer.comments,
        inputComment: state.transactionDisputeReducer.inputComment,
        caseId: state.transactionDisputeReducer.caseId,
        accessToken: state.transactionDisputeReducer.accessToken,
    }
}
const mapDispatchToProps = dispatch => {
    return {
        remoteGetUserProfile: (accessToken) => dispatch(actions.remoteGetUserProfile(accessToken)),
        remoteGetComments: (accessToken, currentCaseId) => dispatch(actions.remoteGetComments(accessToken, currentCaseId)),
        remotePostComments: (accessToken, currentCaseId, input) => dispatch(actions.remotePostComments(accessToken, currentCaseId, input)),
        remotePostAttachment: (accessToken, currentCaseId, identifier, name, size, content) => dispatch(actions.remotePostAttachment(accessToken, currentCaseId, identifier, name, size, content)),
        //getComments: (currentCaseId) => dispatch(actions.getComments(currentCaseId)),
    }
}



export default connect(mapStateToProps, mapDispatchToProps)(Comments);











