import React, { Component } from 'react';
import { connect } from 'react-redux';
import * as actions from '../state/actions';

import InformationTab from '../components/landing-page/InformationTab';
import Comments from '../components/landing-page/Comments';


class CaseDetailsApp extends Component {

  componentDidMount() {
    const accessToken = localStorage.getItem("accessToken");
  //  const accessToken = "31ccfa1e26476c0142068a74f36f48e7";
    if (accessToken != null) {
      this.props.setAccessToken(accessToken);
      this.props.remoteGetUserProfile(accessToken);
    }
    const caseId = localStorage.getItem("caseId");
  //  const caseId = "FR-0000000004";
    if (caseId != null) {
      this.props.setCaseId(caseId);
    }
  }

  render() {
    const caseId = this.props.caseId;
    const accessToken = this.props.accessToken;

    // console.log('caseId at parent '+ accessToken);
    caseContent = null;

    if (caseId != null || accessToken != null) {
      var caseContent =
        <div>
          <div className="col-lg-8">
            <InformationTab />
          </div>
          <div className="col-lg-4">
            <Comments />
          </div>
        </div>;
    }

    return (
      <div className="row">
        {caseContent}
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    userProfile: state.transactionDisputeReducer.userProfile,
    caseId: state.transactionDisputeReducer.caseId,
    accessToken: state.transactionDisputeReducer.accessToken,
  }
}
const mapDispatchToProps = dispatch => {
  return {
    remoteGetUserProfile: (accessToken) => dispatch(actions.remoteGetUserProfile(accessToken)),
    setCaseId: (caseId) => dispatch(actions.setCaseId(caseId)),
    setAccessToken: (accessToken) => dispatch(actions.setAccessToken(accessToken)),
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(CaseDetailsApp);



